.header-section {
	position: relative;
	width: 100%;
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
	}
	.elements-absolute {
		@include min-lg {
			height: 100%;
			.background-element {
				height: 100%;
				img {
					height: 100%;
				}
			}
		}
	}
	.frame {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 110%;
		transform: translate(-50%, -50%);
	}
	.bg-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 90%;
		transform: translate(0, 25%);
		@include max-lg {
			height: 60%;
			transform: translate(0, 10%);
		}
		@include max-md {
			height: 50%;
			transform: translate(0, 0);
		}
		.fog {
			position: absolute;
			top: 0;
			left: 0;
			width: 45%;
			transform: translate(-5%, -25%);
			@include max-lg {
				width: 60%;
				transform: translate(-15%, -20%);
			}
		}
		.bg-2 {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			transform: translate(0, -8%);
			@include max-lg {
			}
			@include max-md {
				transform: translate(0, -20%);
			}
			.bg-3 {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 70%;
				transform: translate(0, -55%);
				@include max-lg {
					transform: translate(0, -45%);
					.bg-fluid {
						object-position: 0% 50%;
					}
				}
			}
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 320px;
		transform: translate(-65%, -10%);
		@include min-md {
			width: 70%;
		}
		@include min-lg {
			width: 40%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 360px;
		transform: translate(68%, -40%);
		@include min-md {
			width: 80%;
			transform: translate(63%, -40%);
		}
		@include min-lg {
			width: 45%;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 65%;
		transform: translate(-30%, 0);
		@include min-md {
			width: 50%;
		}
		@include min-lg {
			width: 30%;
			transform: translate(-20%, 0);
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 65%;
		transform: translate(30%, 0);
		@include min-md {
			width: 50%;
		}
		@include min-lg {
			width: 30%;
			transform: translate(20%, 0);
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 180px;
		transform: translate(-50%, -10%);
		@include min-md {
			width: 240px;
		}
		@include min-lg {
			width: 260px;
		}
		.orn-6 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 120px;
			transform: translate(0, 30%);
			@include min-md {
				width: 160px;
				transform: translate(10%, 30%);
			}
			@include min-lg {
				width: 180px;
			}
			.orn-7 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 120px;
				transform: translate(80%, 30%);
				@include min-md {
					width: 180px;
				}
				@include min-lg {
					width: 220px;
				}
			}
		}
	}
	.orn-8 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 180px;
		transform: translate(50%, 0) scaleX(-1);
		@include min-md {
			width: 240px;
		}
		@include min-lg {
			width: 260px;
		}
		.orn-9 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 120px;
			transform: translate(0, 30%);
			@include min-md {
				width: 160px;
				transform: translate(20%, 20%);
			}
			@include min-lg {
				width: 180px;
			}
			.orn-10 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 140px;
				transform: translate(80%, 20%) scaleX(-1);
				@include min-md {
					width: 180px;
				}
				@include min-lg {
					width: 220px;
				}
			}
		}
	}
	.header-content {
		position: relative;
		height: 100vh;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 4.5rem 0 3rem;
		.header {
			position: relative;
			width: 35%;
			margin: 0 auto;
			@include max-xl {
				width: 40%;
			}
			@include max-lg {
				width: 70%;
			}
			@include max-md {
				width: 100%;
			}
			.header-title {
				position: relative;
				text-align: center;
				&-content {
					position: relative;
					padding: 1rem 2rem;
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
