.thank-section {
	position: relative;
	padding: 5rem 0 0;
	@include max-md {
		padding: 3rem 0 0;
	}
	.frame {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 135%;
		height: 120%;
		transform: translate(-50%, -50%);
		.img-fluid {
			height: 100%;
		}
	}
	.bg-thank {
		top: unset;
		height: 100vh;
	}
	.bg-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 90%;
		transform: translate(0, 25%);
		@include max-lg {
			height: 60%;
			transform: translate(0, 10%);
		}
		@include max-md {
			height: 50%;
			transform: translate(0, 0);
		}
		.fog {
			position: absolute;
			top: 0;
			left: 0;
			width: 45%;
			transform: translate(-5%, -25%);
			@include max-lg {
				width: 60%;
				transform: translate(-15%, -20%);
			}
		}
		.bg-2 {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			transform: translate(0, -8%);
			@include max-lg {
			}
			@include max-md {
				transform: translate(0, -20%);
			}
			.bg-3 {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 70%;
				transform: translate(0, -55%);
				@include max-lg {
					transform: translate(0, -45%);
					.bg-fluid {
						object-position: 0% 50%;
					}
				}
			}
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 320px;
		transform: translate(-65%, -30%);
		@include min-md {
			width: 70%;
			transform: translate(-65%, -50%);
		}
		@include min-lg {
			width: 50%;
			transform: translate(-65%, -65%);
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 360px;
		transform: translate(68%, -50%) rotate(5deg);
		@include min-md {
			width: 70%;
			transform: translate(68%, -70%) rotate(5deg);
		}
		@include min-lg {
			width: 40%;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 65%;
		transform: translate(-30%, 0);
		@include min-md {
			width: 50%;
		}
		@include min-lg {
			width: 30%;
			transform: translate(-20%, 0);
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 65%;
		transform: translate(30%, 0);
		@include min-md {
			width: 50%;
		}
		@include min-lg {
			width: 30%;
			transform: translate(20%, 0);
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 180px;
		transform: translate(-50%, -10%);
		@include min-md {
			width: 240px;
		}
		@include min-lg {
			width: 260px;
		}
		.orn-6 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 120px;
			transform: translate(0, 30%);
			@include min-md {
				width: 160px;
				transform: translate(10%, 30%);
			}
			@include min-lg {
				width: 180px;
			}
			.orn-7 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 120px;
				transform: translate(80%, 30%);
				@include min-md {
					width: 180px;
				}
				@include min-lg {
					width: 220px;
				}
			}
		}
	}
	.orn-8 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 180px;
		transform: translate(50%, 0) scaleX(-1);
		@include min-md {
			width: 240px;
		}
		@include min-lg {
			width: 260px;
		}
		.orn-9 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 120px;
			transform: translate(0, 30%);
			@include min-md {
				width: 160px;
				transform: translate(20%, 20%);
			}
			@include min-lg {
				width: 180px;
			}
			.orn-10 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 140px;
				transform: translate(80%, 20%) scaleX(-1);
				@include min-md {
					width: 180px;
				}
				@include min-lg {
					width: 220px;
				}
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 90%;
		height: auto;
		aspect-ratio: 3/4;
		margin: 0 auto 3rem;
		z-index: 3;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 120px;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		text-align: center;
		padding: 0 0 8rem;
		@include max-md {
			padding: 0 0 5rem;
		}
		.thank {
			position: relative;
			background: radial-gradient(ellipse, rgba($color: #f5f2f2, $alpha: 0.6) 15%, transparent 100%);
			color: var(--color-dark);
			border-radius: var(--border-radius);
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 1rem 5rem;
			}
			&-body {
				position: relative;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
